import React, { useState, useEffect } from "react";
import { Upload, List, Grid, Download, MoreVertical, Filter } from "lucide-react";
import { Card, CardHeader, CardTitle, CardContent } from "../components/ui/Card";
import documentService from "../services/documentService";

const DashboardPage = () => {
  const [viewMode, setViewMode] = useState("list");
  const [recentFiles, setRecentFiles] = useState([]);
  const [storageUsed, setStorageUsed] = useState(0);
  const [storageLimit, setStorageLimit] = useState(15); // GB
  const [fileTypes, setFileTypes] = useState([]);
  const [loading, setLoading] = useState(true); // Add loading state

  // Fetch mock data
  useEffect(() => {
    console.log("useEffect triggered");

    const fetchData = async () => {
      try {
        console.log("Attempting to fetch recent documents...");
        const recentFiles = await documentService.getRecentDocuments();
        console.log("Fetched recent files:", recentFiles);
        setRecentFiles(recentFiles || []);
      } catch (error) {
        console.error("Error fetching dashboard data:", error.message);
      } finally {
        console.log("Setting loading to false");
        setLoading(false);
      }
    };

    fetchData();
  }, []);


  const storagePercentage = (storageUsed / storageLimit) * 100;

  if (loading) {
    return <div className="p-6 max-w-7xl mx-auto text-center">Loading...</div>;
  }

  return (
      <div className="p-6 max-w-7xl mx-auto space-y-6">
        {/* Storage Status */}
        <Card>
          <CardHeader>
            <CardTitle>Storage Status</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="text-sm text-gray-600">
                {storageUsed}GB used from {storageLimit}GB
              </div>
              <div className="w-full h-2 bg-gray-200 rounded-full overflow-hidden">
                <div
                    className="h-full bg-blue-500"
                    style={{ width: `${storagePercentage}%` }}
                />
              </div>
              <div className="flex gap-4">
                {fileTypes.map((type, index) => (
                    <div key={index} className="flex items-center gap-2">
                      <div className={`w-3 h-3 rounded-full ${type.color}`} />
                      <span className="text-sm">{type.type}</span>
                    </div>
                ))}
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Files List */}
        <Card>
          <CardHeader className="flex flex-row items-center justify-between">
            <CardTitle>Files</CardTitle>
            <div className="flex gap-2">
              <button className="p-2 hover:bg-gray-100 rounded-lg">
                <Filter className="w-5 h-5" />
              </button>
              <button
                  className="p-2 hover:bg-gray-100 rounded-lg"
                  onClick={() => setViewMode("list")}
              >
                <List className="w-5 h-5" />
              </button>
              <button
                  className="p-2 hover:bg-gray-100 rounded-lg"
                  onClick={() => setViewMode("grid")}
              >
                <Grid className="w-5 h-5" />
              </button>
            </div>
          </CardHeader>
          <CardContent>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                <tr className="border-b">
                  <th className="text-left py-3 px-4 font-medium">Name</th>
                  <th className="text-left py-3 px-4 font-medium">Date uploaded</th>
                  <th className="text-left py-3 px-4 font-medium">Compliance</th>
                  <th className="text-left py-3 px-4 font-medium">Owner</th>
                  <th className="text-left py-3 px-4 font-medium"></th>
                </tr>
                </thead>
                <tbody>
                {recentFiles.map((file) => (
                    <tr key={file._id} className="border-b">
                      <td className="py-3 px-4">
                        <div className="flex items-center gap-2">
                          <Download className="w-5 h-5 text-gray-500" />
                          <div>
                            <div className="font-medium">{file.title || "Untitled"}</div>
                            <div className="text-sm text-gray-500">
                              <a
                                  href={`http://localhost:5001/${file.fileUrl}`}
                                  target="_blank"
                                  rel="noopener noreferrer"
                                  className="text-blue-500 hover:underline"
                              >
                                Download
                              </a>
                            </div>
                          </div>
                        </div>
                      </td>
                      <td className="py-3 px-4 text-gray-600">
                        {new Date(file.uploadDate).toLocaleDateString()}
                      </td>
                      <td className="py-3 px-4 text-gray-600">
                        {file.complianceStatus?.isCompliant ? "Compliant" : "Non-Compliant"}
                      </td>
                      <td className="py-3 px-4">
                        <div className="flex items-center gap-2">
                          <span>{file.metadata?.ownerName || "Unknown Owner"}</span>
                        </div>
                      </td>
                      <td className="py-3 px-4">
                        <button className="p-1 hover:bg-gray-100 rounded">
                          <MoreVertical className="w-5 h-5" />
                        </button>
                      </td>
                    </tr>
                ))}
                </tbody>
              </table>
            </div>
          </CardContent>
        </Card>
      </div>
  );
};

export default DashboardPage;
