import React, { useEffect, useState } from "react";
import analyticsService from "../services/analyticsService";
import "./AnalyticsReportsPage.css";

const AnalyticsReportsPage = () => {
    const [wordCloudData, setWordCloudData] = useState(null);
    const [trendData, setTrendData] = useState(null);
    const [summaries, setSummaries] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState("");

    useEffect(() => {
        const fetchAnalyticsData = async () => {
            try {
                const [wordCloud, trends, summariesData] = await Promise.all([
                    analyticsService.getWordCloudData(),
                    analyticsService.getTrendData(),
                    analyticsService.getDocumentSummaries(),
                ]);

                setWordCloudData(wordCloud);
                setTrendData(trends);
                setSummaries(summariesData);
            } catch (err) {
                console.error("Error fetching analytics data:", err.message);
                setError(err.message || "Failed to fetch analytics data.");
            } finally {
                setLoading(false);
            }
        };

        fetchAnalyticsData();
    }, []);

    if (loading) {
        return <div className="loading-message">Loading analytics...</div>;
    }

    if (error) {
        return <div className="error-message">{error}</div>;
    }

    return (
        <div className="analytics-reports-page">
            <h1>Analytics Reports</h1>

            {/* Word Cloud Section */}
            <div className="chart-container">
                <h2>Word Cloud</h2>
                <div className="chart-wrapper">
                    {/* Render word cloud chart here */}
                    {wordCloudData ? (
                        <div>{/* Insert word cloud visualization */}</div>
                    ) : (
                        <p>No word cloud data available.</p>
                    )}
                </div>
            </div>

            {/* Trends Section */}
            <div className="chart-container">
                <h2>Document Trends</h2>
                <div className="chart-wrapper">
                    {/* Render trend chart here */}
                    {trendData ? (
                        <div>{/* Insert trend visualization */}</div>
                    ) : (
                        <p>No trend data available.</p>
                    )}
                </div>
            </div>

            {/* Summaries Section */}
            <div className="chart-container">
                <h2>Document Summaries</h2>
                <div className="chart-wrapper">
                    {summaries.length > 0 ? (
                        <ul>
                            {summaries.map((summary, index) => (
                                <li key={index}>
                                    <strong>{summary.title}:</strong> {summary.detail}
                                </li>
                            ))}
                        </ul>
                    ) : (
                        <p>No summaries available.</p>
                    )}
                </div>
            </div>
        </div>
    );
};

export default AnalyticsReportsPage;
