import React, { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";
import authService from "../services/authService";
import "./LoginPage.css";
import logo from "../assets/paperlessiq-logo.png"; // Adjust path if needed


const LoginPage = () => {
    const { user, login } = useContext(AuthContext); // Use AuthContext
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [error, setError] = useState("");
    const navigate = useNavigate();

    // Redirect if already authenticated
    useEffect(() => {
        if (user) {
            navigate("/dashboard", { replace: true });
        }
    }, [user, navigate]);

    const handleLogin = async (e) => {
        e.preventDefault();
        setError("");

        try {
            const response = await authService.login(email, password);
            console.log("Login response data:", response);

            if (response && response.user) {
                login(response.user); // Update state via context
                navigate("/dashboard", { replace: true }); // Redirect
            } else {
                setError("Invalid login response");
                console.error("Login failed: Invalid response", response);
            }
        } catch (error) {
            const errorMessage =
                error.response?.data?.message ||
                error.message ||
                "Login failed";
            setError(errorMessage);
            console.error("Login error:", error);
        }
    };

    return (
        <div className="login-container">
            <img src={logo} alt="PaperlessIQ Logo" className="login-logo"/>
            <form onSubmit={handleLogin}>
                <div className="form-group">
                    <input
                        type="email"
                        placeholder="Email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        required
                    />
                </div>
                <div className="form-group">
                    <input
                        type="password"
                        placeholder="Password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                    />
                </div>
                <button type="submit">Login</button>
                {error && <div className="error-message">{error}</div>}
            </form>
        </div>
    );
};

export default LoginPage;
