import React, { useState, useEffect } from 'react';
import userService from '../services/userService'; // Import the service
import './UserManagementPage.css';

const UserManagementPage = () => {
    const [users, setUsers] = useState([]);
    const [isInvitingUser, setIsInvitingUser] = useState(false); // Modal state
    const [inviteUser, setInviteUser] = useState({ name: '', email: '' }); // Invite form state
    const [setupLink, setSetupLink] = useState(''); // Generated setup link

    useEffect(() => {
        const fetchAllUsers = async () => {
            try {
                const response = await userService.getAllUsers();
                setUsers(response);
            } catch (error) {
                console.error('Error fetching users:', error);
            }
        };

        fetchAllUsers();
    }, []);

    const handleInviteClick = () => {
        setIsInvitingUser(true);
    };

    const handleCloseModal = () => {
        setIsInvitingUser(false);
        setInviteUser({ name: '', email: '' }); // Reset form
        setSetupLink(''); // Clear any setup link
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setInviteUser((prev) => ({
            ...prev,
            [name]: value,
        }));
    };

    const handleInviteSubmit = async (e) => {
        e.preventDefault();
        try {
            const response = await userService.inviteUser(inviteUser); // Call inviteUser API
            setSetupLink(response.setupLink); // Save the setup link
        } catch (error) {
            console.error('Error inviting user:', error);
        }
    };

    return (
        <div className="user-management-page">
            <h1>User Management</h1>
            <button onClick={handleInviteClick} className="add-user-button">
                Invite User
            </button>

            <table>
                <thead>
                <tr>
                    <th>Name</th>
                    <th>Email</th>
                </tr>
                </thead>
                <tbody>
                {users.map((user) => (
                    <tr key={user._id}>
                        <td>{user.name}</td>
                        <td>{user.email}</td>
                    </tr>
                ))}
                </tbody>
            </table>

            {isInvitingUser && (
                <div className="modal">
                    <div className="modal-content">
                        <h2>Invite User</h2>
                        {setupLink ? (
                            <div>
                                <p>Invitation sent! Share this link with the user:</p>
                                <a href={setupLink} target="_blank" rel="noopener noreferrer">
                                    {setupLink}
                                </a>
                                <button onClick={handleCloseModal} className="close-button">
                                    Close
                                </button>
                            </div>
                        ) : (
                            <form onSubmit={handleInviteSubmit}>
                                <label>
                                    Name:
                                    <input
                                        type="text"
                                        name="name"
                                        value={inviteUser.name}
                                        onChange={handleInputChange}
                                        required
                                    />
                                </label>
                                <label>
                                    Email:
                                    <input
                                        type="email"
                                        name="email"
                                        value={inviteUser.email}
                                        onChange={handleInputChange}
                                        required
                                    />
                                </label>
                                <div className="modal-actions">
                                    <button type="submit" className="submit-button">
                                        Invite
                                    </button>
                                    <button
                                        type="button"
                                        onClick={handleCloseModal}
                                        className="cancel-button"
                                    >
                                        Cancel
                                    </button>
                                </div>
                            </form>
                        )}
                    </div>
                </div>
            )}
        </div>
    );
};

export default UserManagementPage;
